@use 'base/variables' as v;
@use 'base/mixins' as m;

.form {
    display: flex;
    flex-direction: column;
    &__field {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }
    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$black, 0.8);
    }
    &__input {
        padding: 1rem;
        border: 1px solid darken(v.$gray, 20%);
        border-radius: 0.5rem;
    }
    &__submit {
        @include m.button(v.$blue, v.$white);
        width: auto;
        align-self: flex-start;

    }
}